import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { BatchOrder } from '../model/batch-order.model';
import { BatchOrdersHttpService } from '@backbase/data-ang/payment-batch';

@Injectable()
export class PaymentsBatchService {
  getBatch(batchOrderId: string): Observable<BatchOrder> {
    return this.batchOrdersHttpService.getBatchOrdersById({ batchOrderId });
  }

  constructor(private readonly batchOrdersHttpService: BatchOrdersHttpService) {}
}
